function getNumberEmoji(num: number): string {
  const numbers = ['0Ô∏è‚É£', '1Ô∏è‚É£', '2Ô∏è‚É£', '3Ô∏è‚É£', '4Ô∏è‚É£', '5Ô∏è‚É£', '6Ô∏è‚É£', '7Ô∏è‚É£', '8Ô∏è‚É£', '9Ô∏è‚É£'];
  return numbers[num];
}

export function numberToEmojiString(num: number): string[] {
  const array = num.toString().split('');
  const res: string[] = [];

  array.forEach(el => {
    res.push(getNumberEmoji(+el));
  });

  return res;
}
